import React, { Component } from "react";

export default class PreRegisterForm extends Component{
	componentDidMount() {
		const script1 = document.createElement('script');
		script1.setAttribute(
			"src",
			"//s3.amazonaws.com/downloads.mailchimp.com/js/mc-validate.js"
		);
		const script2 = document.createElement('script');
		script2.innerhtml =
		`<script type='text/javascript'>(function($) {window.fnames = new Array(); window.ftypes = new Array();fnames[0]='EMAIL';ftypes[0]='email';fnames[1]='FNAME';ftypes[1]='text';fnames[2]='LNAME';ftypes[2]='text';
					$.extend($.validator.messages, {
					  required: "",
					  remote: "",
					  email: "E",
					  url: "URL",
					  date: "",
					  dateISO: "ISO",
					  number: "",
					  digits: "",
					  creditcard: "",
					  equalTo: "",
					  accept: "",
					  maxlength: $.validator.format("{0} "),
					  minlength: $.validator.format("{0} "),
					  rangelength: $.validator.format("{0}  {1} "),
					  range: $.validator.format("{0}  {1} "),
					  max: $.validator.format("{0} "),
					  min: $.validator.format("{1} ")
					});}(jQuery));var $mcj = jQuery.noConflict(true);</script>`;
		document.body.appendChild(script1);
		document.body.appendChild(script2);
	}

	render() {
		const style1 = {
			display: 'none'
		}
		const style2 = {
			position: "absolute",
			left: "-5000px"
		}
		return(
			<div id="mc_embed_signup">
				<form action="https://github.us17.list-manage.com/subscribe/post?u=c738b2ce292af6535fabc6a0e&amp;id=819c7b7844" method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" className="validate" target="_blank" novalidate>
			    <div id="mc_embed_signup_scroll">
						<h2> </h2>
						<div className="indicates-required"><span className="asterisk">*</span> </div>
						<div className="mc-field-group">
							<label htmlFor="mce-EMAIL">  <span className="asterisk">*</span></label>
							<input type="email" value="" name="EMAIL" className="required email" id="mce-EMAIL"/>
						</div>
						<div className="mc-field-group">
							<label htmlFor="mce-FNAME">  <span className="asterisk">*</span></label>
							<input type="text" value="" name="FNAME" className="required" id="mce-FNAME" />
						</div>
						<div className="mc-field-group">
							<label htmlFor="mce-LNAME">  <span className="asterisk">*</span></label>
							<input type="text" value="" name="LNAME" className="required" id="mce-LNAME"/>
						</div>
						<div id="mce-responses" className="clear">
							<div className="response" id="mce-error-response" style={style1}></div>
							<div className="response" id="mce-success-response" style={style1}></div>
						</div>
				    <div style={style2} aria-hidden="true">
				    	<input type="text" name="b_c738b2ce292af6535fabc6a0e_819c7b7844" tabindex="-1" value="" />
				    </div>
				    <div className="clear">
				    	<input type="submit" value="Subscribe" name="subscribe" id="mc-embedded-subscribe" className="button" />
				    </div>
			    </div>
				</form>
			</div>
		)
	}	
}


